<template>
    <div class="address">
        <div class="insert-address">
            <div class="title bold">
                收货地址
            </div>
            <p style="padding: 10px 20px 0 20px"><a class="bold" style="color: #ff0077" @click="$router.go(0)">新增收货地址</a></p>
            <div class="insert-body ">
                <table>
                    <tr>
                        <td class="t-title">
                            <p>收货人<span></span></p>
                        </td>
                        <td class="t-main">
                            <input type="text" v-model="addEdit.truename">
                        </td>
                    </tr>
                    <tr>
                        <td class="t-title">
                            <p>所在地区<span></span></p>
                        </td>
                        <td class="t-main">
                            <ul>
                                <li>
                                    <select name="province" v-model="addEdit.province" @change="SetOption">
                                        <option v-for="(i,k) in provinces" :value="i.region_id" :key="k">
                                            {{i.region_name}}
                                        </option>
                                    </select>
                                </li>
                                <li><select name="city" v-model="addEdit.city" @change="SetOption">
                                    <option v-for="(i,k) in citys" :value="i.region_id" :key="k">{{i.region_name}}
                                    </option>
                                </select></li>
                                <li><select name="town" v-model="addEdit.town" @change="SetOption">
                                    <option v-for="(i,k) in towns" :value="i.region_id" :key="k">{{i.region_name}}
                                    </option>
                                </select></li>
                                <li><select name="county" v-model="addEdit.county" @change="SetOption">
                                    <option v-for="(i,k) in countys" :value="i.region_id" :key="k">{{i.region_name}}
                                    </option>
                                </select></li>
                            </ul>
                        </td>
                    </tr>
                    <tr>
                        <td class="t-title">
                            <p>详细地址<span></span></p>
                        </td>
                        <td class="t-main">
                            <input type="text" style="width: 480px" placeholder="请填写详细地址" v-model="addEdit.address">
                        </td>
                    </tr>
                    <tr>
                        <td class="t-title">
                            <p>手机号码<span></span></p>
                        </td>
                        <td class="t-main">
                            <input type="text" placeholder="请添加手机号码" v-model="addEdit.telephone">
                        </td>
                    </tr>
                    <tr>
                        <td class="t-title"> &nbsp; &nbsp;</td>
                        <td class="t-main">
                            <button class="cursor" @click="saveAddress">保存</button>
                        </td>
                    </tr>
                </table>
            </div>
        </div>
        <div class="address-list">
            <div class="title bold">
                已保存的地址
            </div>
            <div class="table-body">
                <table>
                    <thead>
                    <tr class="ac bold">
                        <td style="width: 80px">收货人</td>
                        <td style="width: 128px">所在地区</td>
                        <td style="width: 300px">详细地址</td>
                        <td style="width: 100px">手机号码</td>
                        <td>操作</td>
                    </tr>
                    </thead>
                    <tbody>
                    <tr class="ac" v-for="(add,key) in AddList" :key="key">
                        <td>{{add.truename}}</td>
                        <td>{{`${add.provinceName}${add.cityName}${add.townName}${add.countyName?add.countyName:''}`}}</td>
                        <td>{{add.address}}</td>
                        <td>{{add.telephone}}</td>
                        <td>
                            <ul>
                                <li><a @click="editAdd(key)">编辑</a></li>
                                <li><a @click="delAddress(add)">删除</a></li>
                                <li><a @click="setDefault(add)">
                                    <span v-if="!add.moren">设为默认</span>
                                    <span v-else>默认地址</span>
                                </a></li>
                            </ul>
                        </td>
                    </tr>
                    </tbody>
                </table>
            </div>
        </div>
    </div>
</template>

<script>
    let display = {
        showRecommend: true,
        nav: 'default',
        header: 'default'
    };
    export default {
        name: "place",
        metaInfo: {
            title: '地址管理',
            meta: [
                {name: 'keywords', content: '福分通'},
                {name: 'description', content: '专注企业员工福利'}
            ]
        },
        data() {
            return {
                AddList: Array,
                addEdit: {
                    truename: '',
                    telephone: '',
                    alladdress: '',
                    province: "",
                    provinceName: '',
                    city: '',
                    cityName: '',
                    town: '',
                    townName: '',
                    county: '',
                    countyName: ''
                },
                provinces: [],
                citys: [],
                towns: [],
                countys: []
            }
        },
        created() {
            this.$store.dispatch('layout/Change', display);
            this.AddressList();
            this.getAddList(1, 'province');
        },
        methods: {
            //编辑地址按钮点击事件
            SetOption(e) {
                let name = e.target.name;
                let value = e.target.value;
                switch (name) {
                    case 'province':
                        this.getAddList(value, 'city');
                        break;
                    case 'city' :
                        this.getAddList(value, 'town');
                        break;
                    case 'town':
                        this.getAddList(value, 'county');
                        break;
                    case 'county':
                        break;
                }
            },
            async editAdd(index) {
                this.addEdit = this.AddList[index];
                document.querySelector('html').scrollIntoView({behavior: "smooth", block: "start", inline: "start"});
                await this.getAddList(1, 'province');
                await this.getAddList(this.AddList[index].province, 'city');
                await this.getAddList(this.AddList[index].city, 'town');
                await this.getAddList(this.AddList[index].town, 'county');
            },
            //
            getAddList(pid, tag) {
                this.yum('api/city/lists', {pid: pid}).then(
                    res => {
                        if (res.data.status === 200) {
                            switch (tag) {
                                case 'province':
                                    this.provinces = res.data.data;
                                    break;
                                case 'city':
                                    this.citys = res.data.data;
                                    break;
                                case 'town':
                                    this.towns = res.data.data;
                                    break;
                                case 'county':
                                    this.countys = res.data.data;
                                    break;
                            }
                        } else {
                            this.bus.$emit('notice', res.data.msg)
                        }
                    }
                )
            },
            /*
            * 获取到地址列表
            * */
            AddressList() {
                this.yum('api/address/lists').then(
                    res => {
                        if (res.data.status === 200) {
                            this.AddList = res.data.data
                        } else {
                            this.bus.$emit('notice', res.data.msg)
                        }
                    }
                )
            },
            saveAddress() {
                let {truename, telephone, province, city, town, county, address, id} = this.addEdit;
                let testPhone = /^1([358][0-9]|4[579]|66|7[0135678]|9[89])[0-9]{8}$/;
                if(!testPhone.test(telephone)){
                    this.bus.$emit('notice','手机号码格式不对！')
                    return
                }
                let data = {
                    truename: truename,
                    telephone: telephone,
                    province: province,
                    city: city,
                    town: town,
                    county: county,
                    address: address,
                    id: id,
                    act: id ? 'update' : 'add'
                };

                this.yum('api/address/save', data).then(res => {
                    if (res.data.status === 200) {
                        this.bus.$emit('notice', res.data.msg);
                        this.$router.go(0)
                    } else {
                        this.bus.$emit('notice', res.data.msg);
                    }
                })
            },
            delAddress(item) {
                let data = {
                    id: item.id
                };
                this.bus.$emit('confirm','是否删除地址信息？');
                this.bus.$on('confirm-ok',function () {
                    this.yum('api/address/del', data).then(res => {
                        if (res.data.status === 200) {
                            this.bus.$emit('notice', res.data.msg);
                            this.AddList = [];
                            this.AddressList()
                        } else {
                            this.bus.$emit('notice', res.data.msg);
                        }
                    })
                }.bind(this))
            },
            setDefault(item){
                let data = {
                    address_id: item.id
                };
                this.bus.$emit('confirm','是否设置为默认的地址信息？');
                this.bus.$on('confirm-ok',function () {
                    this.yum('api/address/setDefault', data).then(res => {
                        if (res.data.status === 200) {
                            this.bus.$emit('notice', res.data.msg);
                            this.AddList = [];
                            this.AddressList()
                        } else {
                            this.bus.$emit('notice', res.data.msg);
                        }
                    })
                }.bind(this))
            }
        }
    }
</script>

<style scoped lang="stylus">
    .insert-address
        position relative
        min-height 388px
        background-color white
        box-shadow 0 0 8px #bbb
        .title
            height 50px
            line-height 50px
            border-bottom 1px solid #ccc
            font-size 16px
            padding 0 10px
            margin 0 10px
        table
            width 580px
            tr
                margin-top 10px
                height 40px
                display inline-block
            .t-title
                width 80px
                height 40px
                line-height 40px
                position relative
                color black
                p
                    text-align justify
                    height 40px
                    span
                        width 100%
                        display inline-block
            .t-main
                width 500px
                padding-left 20px
                input
                    display block
                    height 40px
                    line-height 40px
                    width 300px
                    padding 0 10px
                    outline none
                    border 1px solid #ccc
                    border-radius 4px
                button
                    width 100px
                    height 40px
                    font-size 16px
                    color white
                    background-color #ff0077
                    border none
                    outline none
                ul
                    position relative
                    height 40px
                    li
                        display inline-block
                        height 40px
                        width 70px
                        margin-right 10px
                        select
                            display block
                            width 70px
                            height 40px
                            outline none
                            border-radius 4px

    .address-list
        margin-top 20px
        padding 20px 10px
        background-color white
        box-shadow 0 0 8px #bbb
        .title
            font-size 16px
            padding 0 10px
            margin-bottom 20px
        .table-body
            position relative
            table
                width 100%
                thead
                    background-color #e4e4e4
                tr
                    height 50px
                td
                    border 1px solid #ccc
                li
                    display inline-block
                    margin 0 10px

    .insert-body
        padding 20px


</style>
